import React from 'react'
import styled from 'styled-components'
const Header = () =>{
    return (
        <HeaderSection id="home">
            <div className="header-title">
                <h1>Position : Website Development</h1>
            </div>
        </HeaderSection>
    );
}
const HeaderSection = styled.section`
height: 100vh;
position:relative;
display: flex;
justify-content: center;
color:#fff;
align-items: center;
background: fixed;
background-image: url(./images/banner.jpg);
background-size: cover;
background-position: 20% 100%;   

::after {
    content: "";
    background: #fff;
    height: 200px;
    -webkit-transform: skewY(-5deg);
    -ms-transform: skewY(-5deg);
    transform: skewY(-5deg);
    position: absolute;
    left: 0;
    bottom: -100px;
    right: 0;
    z-index: 1;

}

::before {
    position: absolute;
    content: "";
    height:auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #9E9E9E;
    opacity:0.6;
    z-index: 1;
    
}

.header-title{
    z-index:2;
}
`

export default Header