import React from 'react'
import styled from 'styled-components'

const Navbar = () => {
    
    const handleLink = (e) => {
        e.preventDefault();
        var href = e.target.getAttribute('href');
        var section = document.querySelector(href);
        section.scrollIntoView({
            behavior: 'smooth'

        });
    }

    return (
        <Nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                    <a className="nav-item nav-link" href="#home" onClick={e => handleLink(e)}>Home</a>
                    <a className="nav-item nav-link" href="#technology" onClick={e => handleLink(e)}>Technology</a>
                    <a className="nav-item nav-link" href="#about-me" onClick={e => handleLink(e)}>About me</a>
                    <a className="nav-item nav-link" href="#education" onClick={e => handleLink(e)}>Education</a>
                </div>
            </div>
        </Nav>
    )
}

const Nav = styled.section`
    width:100%;
    z-index:999;
    position:fixed;
    top:0;
    left:0;
    background: #2e2e2e !important;
    
    .navbar-collapse {
        justify-content: center;
    }

    .nav-link {
        padding-left: 1.5rem !important;
        padding-right: 1.5rem !important;
    }

    .nav-item {
        color: #fff;
    }
`

export default Navbar;