import React from 'react'
import styled from 'styled-components'
import ScrollAnimation from 'react-animate-on-scroll'


function Card ({
   title,
   percentage,
   col,
   image,
   experence,
   ...props
}){
  
    return (
        <Container className={ col }>
            <ScrollAnimation animateIn="zoomIn" animateOnce="false">
            <CardPage className="card">
                <HeaderLogo>
                        <img src={ image } alt="" width="100%"/>      
                </HeaderLogo>
                <TitleLogo className="car-body">
                    <h5 className="text-primary">{ title }</h5>
                    <SubTitle color={experence}>{experence}</SubTitle>
                    {/* <div className="progress">
                        <ProgressBar className="progress-bar progress-bar-striped" role="progressbar" width={percentage} aria-valuenow={ percentage } aria-valuemin="0" aria-valuemax="100"></ProgressBar>
                    </div> */}
                </TitleLogo>
            </CardPage>
            </ScrollAnimation>
        </Container>
    )
}

const Container = styled.section`
    display:flexbox;
    align-items:flex-end;

`

const CardPage = styled.section`
    padding: 30px 60px;
    display:flex;
    position:relative;
    justify-content:flex-end;
    

    
`

const HeaderLogo = styled.section`
    display:flex;
    align-items:center;
    width:100%;
    margin-bottom: 30px;
    height:100%;
    img {
        margin:auto;
    }
`

const TitleLogo = styled.section`
    text-align: center;
    h5 {
        font-weight: 700;
    }
    
`
    
const SubTitle = styled.section`
    font-size: 1.1rem !important;
    font-weight: 500;
    color: ${(experence) => experence.color === "Advance" ? "#20c97b" : "#ffc107" };

`

const ProgressBar = styled.section`
    width: ${(percentage) => percentage};
`


export default Card;