import React from 'react'
import styled from 'styled-components'
import ScrollAnimation from 'react-animate-on-scroll'


const Abouteme = () => {
    return (
        <PortfolioSection id="about-me">
            <ScrollAnimation animateIn="fadeInUp" animateOnce="false">
                <h2 className="section-title-header">About me</h2>
            </ScrollAnimation>
            <ScrollAnimation animateIn="bounce" duration="1.5">
                <div className="block-image">
                    <img src="./images/my-image.jpg" alt=""/>
                </div>
            </ScrollAnimation>
            <div className="block-content">
                <p className="title-about text-primary">Teerach Mincharoen</p>
                <hr/>
                <p><b>Date of birth :</b> 28 September 1997</p>
                <p><b>Age :</b> 23 years old</p>
                <p><b>Personality :</b> Fast learner, Responsible, Friendly</p>
                <p><b>Religion :</b> Islam</p>
                <p><b>Height / Weight :</b> 175cm / 60kgs.</p>
                <p><b>Nationality :</b> Thai</p>
                <p><b>Health :</b> Excellence</p>
                <p><b>Contact :</b> Tel 086-714-0083</p>
                <p><b>Email :</b> teerach.min@gmail.com</p>
            </div>
        </PortfolioSection>
    )
}

const PortfolioSection = styled.section`
        padding: 15% 0 0;
        text-align: center;
        margin:0 0 15%;
        p {
            margin: 0 0 5px;
        }

        .block-image{
            width:300px;
            height:300px;
            margin:auto;
            border-radius: 50%;
            margin-top:4rem;
            margin-bottom:20px;
            overflow:hidden;
            position: relative;


            img {  
                position:absolute;
                width: 150%;
                top:0;
                left:-90px;

            }
        }

        .block-content{
            max-width: 700px;
            width:100%;
            margin:auto;
            .title-about {
                font-size : 1.5rem;
            }

            hr {
                color: #575757;
            }
        }
        
    `



export default Abouteme;