import React from 'react'
import styled from 'styled-components'
import Card from '../components/Card'
import ScrollAnimation from 'react-animate-on-scroll'


var experenceData = [
    { image: './images/logo_html.png', title:'HTML', experence:"Advance"},
    { image: './images/logo_css.png', title: 'CSS', experence:'Advance'},
    { image: './images/logo_php.png', title: 'PHP', experence:"Advance"},
    { image: './images/logo_mysql.png', title: 'Mysql', experence:"Advance"},
    { image: './images/logo_codeigniter.png', title: 'Codeigniter', experence:"Advance"},
    { image: './images/logo_laravel.png', title: 'Laravel', experence:"Basic"},
    { image: './images/logo_javascript.png', title: 'Java Script', experence:"Basic"},
    { image: './images/logo_nodejs.png', title: 'Node JS', experence: 'Basic'},
    { image: './images/logo_react.png', title: 'React JS', experence: 'Basic'},
];


const Technology = () => {
    return (
        <TechnologySection id="technology" className="container">
            <ScrollAnimation animateIn="fadeInUp" animateOnce="false">
            <h3 className="section-title-sub text-primary">Experence</h3>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp" animateOnce="false">
            <h2 className="section-title-header">Technology</h2>
            </ScrollAnimation>
            <div className="technology-block">
                <div className="row">
                { experenceData.map((item, key) => {
                    return (
                            <Card key={key++} image={item.image} col="col-lg-4 col-md-6 col-sm-12 col-xs-12 mb-4 mb-lg-5" title={item.title} experence={item.experence} />
                    )
                })}
                </div>
            </div>
        </TechnologySection>
    );
}

const TechnologySection = styled.section`
        padding-top: 100px;
        text-align:center;
        max-width: 1100px;

        h2 {
            margin:0 0 3% 0;
        }

        .row {
            display:flex;
            align-items: stretch;
        }

    `

export default Technology;