import React from 'react'
import styled from 'styled-components'
import ScrollAnimation from 'react-animate-on-scroll'


const Timeline = () => {
    return (
        <TimelineSection id="education">
            <div className="container">
                <div className="title-timeline">
                    <ScrollAnimation animateIn="fadeInUp" animateOnce="false">
                        {/* <h3 className="section-title-sub text-primary">School</h3> */}
                    <h2 className="section-title-header">Education</h2>
                    </ScrollAnimation>
                </div>
                <div className="timeline">
                    <div className="block-timeline left">
                        <div className="content">
                            <p>2016 - 2018</p>
                            <ScrollAnimation animateIn="fadeInLeft" animateOnce="false">
                                <h3 className="title-content">High Vocational Certificate</h3>
                                <p>Intrachai Commercial College</p>
                                <p>(Information Technology)</p>
                                <p>GPA 3.50</p>
                            </ScrollAnimation>
                        </div>
                    </div>
                    <div className="block-timeline right">
                        <div className="content">
                            <p>2013 - 2016</p>
                            <ScrollAnimation animateIn="fadeInRight" animateOnce="false">
                            <h3 className="title-content">Vocational Certificate</h3>
                            <p>Intrachai Commercial College</p>
                            <p>(Business Computer)</p>
                            <p>GPA 3.71</p>
                            </ScrollAnimation>
                        </div>
                    </div>
                    <div className="block-timeline left">
                        <div className="content">
                            <p>2010 - 2013</p>
                            <ScrollAnimation animateIn="fadeInLeft" animateOnce="false">
                            <h3 className="title-content">Junior High School</h3>
                            <p>Anwarulislam School</p>
                            </ScrollAnimation>
                        </div>
                    </div>
                    <div className="block-timeline right">
                        <div className="content">
                            <p>2004 - 2010</p>
                            <ScrollAnimation animateIn="fadeInRight" animateOnce="false">
                            <h3 className="title-content">Grade 1-6</h3>
                            <p>Param 9 Kanchanapisek</p>
                            </ScrollAnimation>
                        </div>
                    </div>
            </div>
            </div>
        </TimelineSection>
    )
}

const TimelineSection = styled.section`
text-align:center;
background: #f5f5f5;

.title-timeline {
    padding: 15% 0 2%;
    // background : #f9f9f9 !important;
}

.timeline {
    margin:30px 0 0;
    width:100%;
    position: relative;
    min-height:100vh;

    .title-content {
        font-size: 1.5rem;
    }
}

.timeline::after {
    content: '';
    position: absolute;
    width: .25rem;
    background-color: #ebebeb;
    top: 0;
    border-radius: 50px;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}

/* block-timeline around content */
.block-timeline {
    padding: 15px 40px 50px;
    position: relative;
    // background-color: inherit;
    width: 50%;
    
}

/* The circles on the timeline */
.block-timeline::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -12px;
    background-color: #007eff;
    border: 7px solid #f5f5f5;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}

.content {
    // background-color: white;
    position: relative;
    // box-shadow: 0 0.125rem rgba(0,0,0,.01);
    text-align: left;
    margin-bottom: 10%;

    p {
        margin: 0;
        font-size: 1rem;
    }

}

/* Place the container to the left */
.left {
    left: 0;
    .content {
        text-align:right;
    }
}

/* Place the container to the right */
.right {
    left: 50%;
}


/* Fix the circle for containers on the right side */
.right::after {
    left: -13px;
}


@media screen and (max-width: 900px) {
/* Place the timelime to the left */
    .timeline::after {
        left: 31px;
    }

    .block-timeline {
        width: 100%;
        padding-left: 70px;
        padding-right: 25px;
    }

    .block-timeline::before {
    left: 60px;
        border: medium solid white;
        border-width: 10px 10px 10px 0;
        border-color: transparent white transparent transparent;
    }

    .left::after, .right::after {
        left: 18px;
    }

    .right {
        left: 0%;
    }

    .left {
        .content {
            text-align: left !important; 
        } 
    }

}

`

export default Timeline;